@import 'variables';

body {
  padding-bottom: 0;
}

.jumbotron {
  background-color: $white;
}

.canvas {
  background-color: $gallery;
  height: auto;
  width: 100%;
}

.time-form {
  margin-bottom: 1rem;
  text-align: center;
}

.form-inline {
  .input-group {
    margin-bottom: .5rem;
    width: 100%;
  }
}

.form-inline {
  .input-group {
    .input-group-addon {
      width: 28%;
    }
  }
}

.clock-panel {
  background-color: $gallery;
  border: 1px solid $silver;
  border-radius: .5rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.clock-panel-2 {
  display: none;
}

.angle-label {
  display: inline-block;
  font-size: 2rem;
  line-height: 1.8;
  margin-bottom: 1rem;
  width: 100%;
}

@media (min-width: 768px) {
  body {
    padding: 1rem 1rem 1.5rem;
  }

  .form-inline {
    .input-group {
      .input-group-addon {
        width: 50%;
      }
    }
  }

  .clock-panel,
  .angle-label {
    margin-bottom: 0;
  }
}

@media (min-width: 992px) {
  .form-inline {
    .input-group {
      margin-bottom: 0;
    }
  }

  .form-inline {
    .input-group {
      .input-group-addon {
        width: 59%;
      }
    }
  }
}